xt-decoration: underline;\n}\n\n.list_item_title a {\n    text-decoration: none;\n    color: inherit;\n}\n\n.list_item_desc {\n    margin-top: -30px;\n    font-size: 12px;\n    line-height: 1.5em;\n}\n\n.card {\n    box-shadow: 0px 0px 10px rgba(122, 120, 120, 0.1);\n    width: calc(33.33% - 16px);\n    border: 1px solid #36332c;\n    border-radius: 7px;\n    overflow: hidden;\n    position: relative;\n    -webkit-border-radius: 7px;\n    -moz-border-radius: 7px;\n    -ms-border-radius: 7px;\n    -o-border-radius: 7px;\n}\n\n.city {\n    position: absolute;\n    top: 8px;\n    left: 16px;\n    border-radius: 2px;\n    background-color: #135ea8;\n    padding: 5px 10px 5px;\n    color: rgb(19, 17, 17);\n    font-weight: 500;\n    text-transform: uppercase;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -ms-border-radius: 2px;\n    -o-border-radius: 2px;\n}\n\n\n/* codingan submission 1 menampilkan content restaurant */\n\n.card_content {\n    padding: 16px;\n}\n\n.card:hover {\n    background-color: #5d9469;\n    scroll-timeline: 0.5;\n    opacity: 0.9;\n    color: #b91c1c;\n}\n\n.card_thumb {\n    width: 100%;\n    height: 200px;\n    object-fit: cover;\n}\n\n.card_rating {\n    font-size: 12px;\n    text-transform: uppercase;\n    color: #141313;\n    font-weight: 500;\n    text-align: left;\n}\n\n.card_rating_number {\n    color: #38be8b;\n    text-decoration: none;\n    font-weight: bold;\n    min-width: 44px;\n    min-height: 44px;\n    display: inline-block;\n}\n\n.card_title {\n    font-weight: 500;\n    font-size: 18px;\n    margin-top: 10px;\n    transition: 0.3s opacity;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n}\n\n.card_title:hover {\n    opacity: 0.7;\n    text-decoration: underline;\n}\n\n.card_title a {\n    text-decoration: none;\n    color: #333;\n    font-size: 25px;\n    font-weight: bold;\n    min-width: 44px;\n    min-height: 44px;\n    display: inline-block;\n    padding: 44px;\n}\n\n.card_desc {\n    margin