00&display=swap\");\n@import url(\"https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap\");\n@import url(\"https://fonts.googleapis.com/css2?family=Sigmar&display=swap\");\n* {\n    padding: 0;\n    margin: 0;\n    box-sizing: border-box;\n}\n\nhtml {\n    scroll-behavior: smooth;\n}\n\nbody {\n    width: 100%;\n    font-family: Arial, Helvetica, sans-serif;\n    color: #d16464;\n    margin: 0;\n    display: grid;\n    grid-template-rows: auto 1fr auto;\n    font-size: 14px;\n    background-color: #6eaf61;\n    align-items: start;\n    min-height: 100vh;\n    margin: 0;\n    padding: 0;\n}\n\n\n/* maincontent */\n\n.skip-link {\n    position: absolute;\n    top: -40px;\n    left: 0;\n    background-color: #bf1722;\n    color: white;\n    padding: 8px;\n    z-index: 100;\n}\n\n.skip-link:focus {\n    top: 0;\n}\n\na {\n    text-decoration: none;\n    color: inherit;\n}\n\n.nav {\n    width: 100%;\n    margin-left: auto;\n    margin-right: auto;\n    text-align: center;\n    left: 0;\n    top: 0;\n}\n\n.navlist {\n    width: 100%;\n    padding: 0;\n    margin: 0;\n}\n\n.navitem {\n    box-sizing: border-box;\n    display: inline-block;\n    width: 24%;\n    text-align: center;\n    line-height: 24px;\n    text-transform: uppercase;\n}\n\n.nav a {\n    display: inline-block;\n    padding: 1.3em;\n    text-decoration: none;\n    color: #000000;\n    min-width: 44px;\n    min-height: 44px;\n    display: inline-block;\n}\n\n.nav a:hover {\n    text-decoration: underline;\n    color: #d41c1c;\n}\n\n\n/* header */\n\nheader {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 0.1fr;\n    background-color: #40a57b;\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);\n    min-width: 44px;\n    min-height: 44px;\n}\n\n.header {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    height: max-content;\n    background-color: #2a9da5;\n    min-width: 44px;\n    min-height: 44px;\n}\n\n.headermenu {\n    font-size: 25px;\n    margin: 10px auto;\n    padding: 10px;\n    display: inline-block;\n    min-width: 44px;\n    min-height: 44px;\n    display: inline-block;\n    background-color: #45965f;\n    z-index: 100;\n}\n\n.logo {\n    width: 20%;\n    margin: 20px;\n    min-width: auto;\n    max-width: 500px;\n    font-family: \"Pacifico\", cursive;\n    font-size: 30px;\n    color: #b82f2f;\n    display: flex;\n}\n\n\n/* Navbar */\n\nnav ul {\n    display: flex;\n    flex-wrap: wrap;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\nnav li {\n    margin: 0.3rem;\n    font-size: 17px;\n    font-family: \"Franklin Gothic Medium\", \"Arial Narrow\", Arial, sans-serif;\n}\n\nnav a {\n    display: block;\n    padding: 1.5rem;\n    text-decoration: none;\n    color: #af3737;\n    border-radius: 5px;\n    transition: background-color 0.3s ease;\n    -webkit-transition: background-color 0.3s ease;\n    -moz-transition: background-color 0.3s ease;\n    -ms-transition: background-color 0.3s ease;\n    -o-transition: background-color 0.3s ease;\n    min-width: 44px;\n    min-height: 44px;\n}\n\nnav a:hover {\n    background-color: #3bb463;\n    color: #ffffff;\n}\n\nnav ul li a {\n    display: block;\n    padding: 25px;\n    color: #fff;\n    text-decoration: none;\n    min-width: 44px;\n    min-height: 44px;\n}\n\nh2 {\n    color: #000;\n    text-align: center;\n}\n\nmain {\n    width: 100%;\n    margin: 0 auto;\n    font-family: \"Open Sans\", sans-serif;\n}\n\n\n/* content */\n\n.content {\n    padding: 32px;\n    font-family: \"Poppins\", sans-serif;\n    color: #000;\n    width: 100%;\n}\n\n.content h2 {\n    margin-bottom: 20px;\n}\n\n.content .content-heading {\n    font-family: \"Sigmar\", cursive;\n    font-weight: normal;\n    color: red;\n}\n\n.content .list-menu {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    box-sizing: border-box;\n    gap: 20px;\n    margin: auto;\n}\n\n.hero {\n    display: grid;\n    align-items: center;\n    justify-content: center;\n    height: 400px;\n    width: 100%;\n    background-repeat: no-repeat;\n    text-align: center;\n    background-size: cover;\n    background-position: center;\n    position: relative;\n    left: 0;\n    top: 0;\n}\n\n._heroinner {\n    display: flex;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    text-align: center;\n    font-family: \"Sigmar\", cursive;\n    color: red;\n}\n\n._herosub {\n    font-size: 24px;\n    font-weight: 400;\n}\n\n.content {\n    padding: 32px;\n}\n\n.resto {\n    width: 100%;\n    text-align: center;\n    margin-top: 25px;\n    margin-bottom: 45px;\n}\n\n.resto_label {\n    width: 100%;\n    font-size: 20px;\n    font-weight: 400;\n}\n\n.resto_label::after {\n    content: \"\";\n    margin-top: 16px;\n    display: block;\n    border-bottom: 1px solid #c44141;\n}\n\n\n/* restaurant */\n\n.restaurant {\n    margin: 0 auto;\n    width: 100%;\n    max-width: 800px;\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 18px 16px;\n}\n\n.restaurant .restaurant-poster {\n    width: 100%;\n    max-width: 400px;\n}\n\n.restaurant .restaurant-info h4 {\n    margin: 8px 0;\n}\n\n\n/* restaurant Item */\n\n.restaurant-item {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    overflow: hidden;\n    background-color: #40a57b;\n}\n\n.restaurant-item-header {\n    position: relative;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-header-poster {\n    width: 100%;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-header-rating {\n    position: absolute;\n    padding: 8px;\n    top: 0;\n    left: 0;\n    display: inline-block;\n    background-color: rgb(72, 207, 144);\n    color: red;\n    font-family: \"Open Sans\", sans-serif;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-header-rating .restaurant-item-header-rating-score {\n    margin-left: 10px;\n}\n\n.restaurant-item .restaurant-item-